.arrowUp__wrap {
    
    position: sticky;
    float: right;
    z-index: 5;
    bottom: 50px;
    width: 70px;
    height: 70px;
   
    cursor: pointer;
    border: 2px solid #000;
    border-radius: 50%;
    padding: 10px 10px 15px;
    img {
        width: 100%;
        height: 100%;
    }
    display: none;
}
.visible {
    display: block;
}